import React, {Component} from 'react';
import cise from 'cytoscape-cise';
import cytoscape from 'cytoscape';

// registra layout
cytoscape.use(cise);

/**
 * O cytoscape nÃ£o foi implementado sob a filosofia declarativa.
 * Sua abordagem imperativa gera algumas dificuldades quando integramos com o React.
 * Esta classe Ã© um wrapper para esconder essa integraÃ§Ã£o meio "deselegante".
 *
 * AtÃ© tentei usar um wrapper jÃ¡ pronto, https://github.com/plotly/react-cytoscapejs,
 * mas tinha muitos bugs e nÃ£o quis perder tempo.
 */
class WordGraph extends Component {

    constructor(props) {
        super(props);
        this.graph = React.createRef();
    }

    componentDidMount() {
        const {onClickNode, onClickEdge} = this.props;

        this.cy = cytoscape({
            container: this.graph.current,
            elements: [],
            style: [
                {
                    selector: 'node',
                    style: {
                        'background-color': ele => ele.data('type') === 'dominio' ? '#CCC' : 'green',
                        'label': 'data(label)'
                    }
                },

                {
                    selector: 'edge',
                    style: {
                        'width': 3,
                        'line-color': '#ccc'
                    }
                }
            ]
        });

        this.cy.on('tap', 'node', function () {
            onClickNode(this);
        });

        this.cy.on('tap', 'edge', function (e) {
            console.log(e.renderedPosition);
            onClickEdge(this);
        });
    }

    draw(context, elements) {
        if (!this.cy) return;

        const formattedElements = toCytoscapeFormat(elements);

        console.log(formattedElements.nodes, "nodes");
        console.log(formattedElements.clusters, "clusters");

        // removemos nÃ³s anteriores, pois podemos ter alterado alguma meta informaÃ§Ã£o deles e queremos sobrescrever
        this.cy.remove('node');
        this.cy.add(formattedElements);

        this.cy.layout({
            name: 'cise',
            // animate: 'end', // nÃ£o funcionou bem com o cy.fit() do ready
            fit: false,
            nodeRepulsion: 5,
            clusters: formattedElements.clusters, // clusters sÃ£o os grupos do cise layout: os cÃ­rculos
            ready: () => {
                // faz o viewport exibir os nodes do contexto atual, i.e, a Ã­ltima palavra escolhida
                this.cy.fit(this.cy.$(`[context='${context}']`));
            }
        }).run();
    }


    shouldComponentUpdate(nextProps, nextState, nextContext) {
        return this.props.elements !== nextProps.elements;
    }

    render() {
        const {context, elements} = this.props;

        // FIXME usar um callback adequado
        this.draw(context, elements);

        return (
            <div className="WordGraph" ref={this.graph}/>
        );
    }
}

const toCytoscapeFormat = ({nodes, edges, clusters}) => ({
    nodes: nodes.map(it => ({data: it})),
    edges: edges.map(it => ({data: it})),
    clusters,
});


export default WordGraph;